.projectsBlock {
    min-height: 100vh;
    background-color: #fabbff;
}

.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
}

.title {
    background-color: #eeeef5;
}

.projects {
    display: flex;
    justify-content: space-between;
    border: 1px solid;
    flex-wrap: wrap;
    gap: 20px;
}

@media (max-width: 1030px) {
    .projects {
        justify-content: center;
    }
}