.footer {
    min-height: 30vh;
    background-color: #d4ffc0;
}

.footerContainer {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.social {
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    padding: 0;
    gap: 30px;
}

.imgWrapper {
    width: 50px;
    height: 50px;
    background-color: #99d0fb;
}

@media (max-width: 460px) {
    .social {
        flex-direction: column;
    }
}